import './TasksHeader.css';
import { useEffect, useState } from 'react';

const TasksHeader = ({ sortTasksByName }) => {
	const [isSort, setIsSort] = useState(false);
	const [state, setState] = useState(true);
	const handleClick = () => {
		if (!isSort) setIsSort(true);
		setState(!state);
	};

	useEffect(() => {
		if (!isSort) return;
		sortTasksByName(state);
	}, [state, isSort]);

	return (
		<div className="task tasksHeader">
			<div className="task__container tasksHeader__container">
				<button
					className="task__name-container tasksHeader__name-container"
					onClick={handleClick}
				>
					
					{state ? (
						<svg
							width="8"
							height="15"
							viewBox="0 0 14 7"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								fillRule="evenodd"
								clipRule="evenodd"
								d="M0 0L7 7L14 0H0Z"
								fill="#000"
							/>
						</svg>
					) : (
						<svg
							width="8"
							height="15"
							transform="rotate(180)"
							viewBox="0 0 14 7"
							fill="none"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								fillRule="evenodd"
								clipRule="evenodd"
								d="M0 0L7 7L14 0H0Z"
								fill="#000"
							/>
						</svg>
					)}
				</button>
				<span className="task__project"></span>
				<span className="task__matrix-wrapper"></span>
				<span className="task__deadline"></span>
				<span className="task__timer-wrapper"></span>
			</div>
			<span className="tasksHeader__timer"></span>
		</div>
	);
};

export default TasksHeader;
